@font-face {
	font-family: "Blogger-Light";
	src: url(/online-store-react-spa/static/media/Blogger-Light.b887f73c.otf) format("truetype");
	font-style: normal;
	font-weight: normal;
}

@font-face {
	font-family: "Modak";
	src: url(/online-store-react-spa/static/media/Modak.9b6bdd26.ttf) format("truetype");
	font-style: normal;
	font-weight: normal;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Blogger-Light";
}

:root {
	--sel-color: #62FFFC;
}

body {
	background-color: white;
	height: 100%;
	overflow-x: hidden;
}

.container {
	height: 100%;
	margin: 0 auto;
	padding: 0 0.5rem;
	max-width: 1360px;
	box-sizing: content-box;
}

a, select {
	text-decoration: none;
	color: black;
}

li {
	list-style: none;
}

button {
	border: none;
	background: unset;
}

button:hover {
	cursor: pointer;
}

.hidden {
	display: none !important;
}


/* ------------Scrollbar style------------ */
div::-webkit-scrollbar,
menu::-webkit-scrollbar,
ul::-webkit-scrollbar {
	width: 0.3vw;
	background-color: rgba(192, 192, 192, 1);
}


div::-webkit-scrollbar-thumb,
menu::-webkit-scrollbar-thumb,
ul::-webkit-scrollbar-thumb {
	background-color: rgba(128, 128, 128, 0.7);
	border-radius: 9em;
}

div::-webkit-scrollbar-thumb:hover,
menu::-webkit-scrollbar-thumb:hover,
ul::-webkit-scrollbar-thumb:hover {
	background-color: rgba(128, 128, 128, 1);
}

/* mozilla */
div,
menu,
ul {
	scrollbar-color: rgba(128, 128, 128, 0.7) rgba(225, 225, 225, 0.5);
	scrollbar-width: thin !important;
}


@media only screen and (min-width: 769px) {
	.checkbox span {
		margin-left: 0.7vw;
	}

	.checkbox input {
		height: 1vw;
		width: 1vw;
	}

	.checkbox input+span::before {
		height: 1vw;
		width: 1vw;
		border-radius: 0.2vw;
	}

	.srch-hint {
