    .goods-page-images ul li.active {
        border: 2px solid black;
    }

    .goods-page-images figure {
        position: relative;
        padding-top: 100%;
        border: 1px dotted grey;
        border-radius: 0.5rem;
        overflow: hidden;
    }

    .goods-page-images img {
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        max-width: 100%;
        max-height: 100%;
    }

    /* Ad page information */
    .goods-page-info {
        grid-area: info;
        display: flex;
        flex-direction: column;
        grid-row-gap: 6vw;
        row-gap: 6vw;
    }

    .goods-page-info h2 {
        font-size: 8vw;
        padding-bottom: 0 0 0.5rem 1rem;
        border-bottom: 2px solid black;
    }

    .goods-page-info figcaption {
        font-size: 6vw;
        font-weight: bold;
        margin-bottom: 1vw;
    }

    .goods-page-info p {
        font-size: 4.4vw;
    }

    .goods-page-info ul {
        display: flex;
        flex-direction: column;
        grid-row-gap: 4vw;
        row-gap: 4vw;
        padding-right: 2vw;
    }

    .goods-page-info li {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        background-image: linear-gradient(to right, #000 10%, rgba(255, 255, 255, 0) 0%);
        background-position: 0 90%;
        background-size: 8px 1px;
        background-repeat: repeat-x;
        width: 100%;

    }

    .goods-page-info li span {
        background-color: #fff;
        font-weight: bold;
        font-size: 1.1rem;
    }

    /* To cart button area */
    .goods-page-tocart-wrap {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        position: relative;
        height: 20vw;
    }

    .goods-page-tocart-wrap button {
        position: absolute;
        display: flex;
        flex-direction: row;
        justify-content: center;
        grid-column-gap: 4vw;
        -webkit-column-gap: 4vw;
                column-gap: 4vw;
        font-size: 8vw;
        padding: 4vw 0;
        width: 80%;
        background-color: black;
        color: white;
        border-radius: 0.5rem;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    }

    .goods-page-tocart-wrap button:hover {
        box-shadow: none;
        border: 2px solid white;
        color: var(--sel-color);
    }
    .goods-page-tocart-wrap button:hover svg {
        fill: var(--sel-color);
    }

    .goods-page-tocart-wrap svg {
        width: 10vw;
        height: 10vw;
        fill: white;
    }
}
.delivery-returns {
    display: flex;
    flex-direction: column;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    padding: 2rem 0;
}
.delivery-returns h1, .delivery-returns h2 {
    text-align: center;
    font-size: 2.2rem;
    margin-bottom: 2rem;
}
.delivery-returns p {
    font-size: 1.2rem;
    margin-bottom: 1rem;
    font-weight: bold;
}

.delivery-returns figure {
    padding: 1rem;
    margin-bottom: 1rem;
    border-bottom: 1px dotted grey;
    background-color: rgba(225,225,225,0.3);
}
.delivery-returns figcaption {
    font-size: 1.4rem;
    font-weight: bold;
    margin-bottom: 1rem;
}
